import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
	ICompetenceLevelModel,
	ICompetenceLevelRequest,
} from "app/org-management-setup/models/competence-level";
import { CompetenceLevelService } from "app/org-management-setup/services/competence-level.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
	selector: "app-competence-level",
	templateUrl: "./competence-level.component.html",
	styleUrls: ["./competence-level.component.scss"],
})
export class CompetenceLevelComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
	optionalColomns: string[] = ["en_Desc", "ar_Desc"];

	dataSource: MatTableDataSource<ICompetenceLevelModel>;
	isLoaded: boolean = false;
	constructor(public competenceLevelService: CompetenceLevelService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			ar_name: [null, [Validators.required, CustomValidator.whiteSpace]],
			en_name: [null, [Validators.required, CustomValidator.whiteSpace]],
			Ar_Desc: [null],
			En_Desc: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId() {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.competenceLevelService
			.getList(params)
      .pipe(this.cancelRequest())
			.subscribe((res: IResponseList<ICompetenceLevelModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: ICompetenceLevelRequest = {
				ID: this.form.value.id,
				Ar_Name: this.form.value.ar_name,
				En_Name: this.form.value.en_name,
				Ar_Desc: this.form.value.Ar_Desc,
				En_Desc: this.form.value.En_Desc,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: ICompetenceLevelRequest) {
		this.showLoading();
		this.competenceLevelService.create(model).subscribe((res: IResponse<ICompetenceLevelModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: ICompetenceLevelRequest) {
		this.showLoading();
		this.competenceLevelService.update(model).subscribe((res: IResponse<ICompetenceLevelModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize); // current page after update
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	competenceLevelToEdit(competenceLevel: ICompetenceLevelModel) {
		this.form.patchValue(competenceLevel);
		this.formCtrls.ar_name.setValue(competenceLevel.ar_Name);
		this.formCtrls.en_name.setValue(competenceLevel.en_Name);
		this.formCtrls.Ar_Desc.setValue(competenceLevel.ar_Desc);
		this.formCtrls.En_Desc.setValue(competenceLevel.en_Desc);
		// console.log(this.form.value);
	}

	onDelete(competenceLevel: ICompetenceLevelModel): void {
		let msg = this.msgTranslate(competenceLevel.ar_Name, competenceLevel.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(competenceLevel);
		});
	}

	delete(competenceLevel: ICompetenceLevelModel) {
		this.showLoading();
		this.competenceLevelService.Delete(competenceLevel.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (competenceLevel.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}
}
