import { JobFamiliesComponent } from "./components/job-families/job-families.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { GradesComponent } from "./components/grades/grades.component";
import { OrganizationsComponent } from "./components/organizations/organizations.component";
import { CompetenceLevelComponent } from "./components/competence-level/competence-level.component";
import { CompetenceTypeComponent } from "./components/competence-type/competence-type.component";
import { DisclaimerComponent } from "./components/disclaimer/disclaimer.component";
import { CustodiesComponent } from "./components/custodies/custodies.component";
import { OrganizationChartComponent } from "./components/organization-chart/organization-chart-component/organization-chart.component";
import { OrganizationTypesComponent } from "./components/organization-types/organization-types.component";
import { ViewsManager } from "app/shared/resources/views-manager";

const routes: Routes = [
	{
		path: "grades",
		component: GradesComponent,
		data: { view: ViewsManager.grades },
	},
	{
		path: "job-families",
		component: JobFamiliesComponent,
		data: { view: ViewsManager.job_families },
	},
	{
		path: "organization-types",
		component: OrganizationTypesComponent,
		data: { view: ViewsManager.organization_types },
	},
	{
		path: "organizations",
		component: OrganizationsComponent,
		data: { view: ViewsManager.organizations },
	},
	{
		path: "competence-level",
		component: CompetenceLevelComponent,
		data: { view: ViewsManager.competence_level },
	},
	{
		path: "competence-type",
		component: CompetenceTypeComponent,
		data: { view: ViewsManager.competence_type },
	},
	{
		path: "disclaimer-template",
		component: DisclaimerComponent,
		data: { view: ViewsManager.disclaimer_template },
	},
	{
		path: "custodies",
		component: CustodiesComponent,
		data: { view: ViewsManager.custodies },
	},

	{
		path: "OrganizationChart",
		component: OrganizationChartComponent,
		data: { view: ViewsManager.OrganizationChart},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class OrgManagementSetupRoutingModule {}
