import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IGradeModel, IGradeRequest } from "app/org-management-setup/models/grades";
import { GradesService } from "app/org-management-setup/services/grades.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
@Component({
	selector: "app-grades",
	templateUrl: "./grades.component.html",
	styleUrls: ["./grades.component.scss"],
})
export class GradesComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	submitted: boolean = false;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"en_Name",
		"ar_Name",
		"miN_SALARY",
		"middel_Salary",
		"maX_SALARY",
		"actions",
	];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];
	dataSource: MatTableDataSource<IGradeModel>;

	constructor(public gradesService: GradesService) {
		super();
	}

	ngOnInit(): void {
		this.form = this.fb.group({
			id: [0, { nonNullable: true }],
			ar_name: [null, [Validators.required, CustomValidator.whiteSpace]],
			en_name: [null, [Validators.required, CustomValidator.whiteSpace]],
			miN_SALARY: [null, [CustomValidators.gt(0), CustomValidators.digits]],
			middel_Salary: [null, [CustomValidators.gt(0), CustomValidators.digits]],
			maX_SALARY: [null, [CustomValidators.gt(0), CustomValidators.digits]],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId() {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
	}

	getList(page: number, pageSize?: number) {
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
		};
		this.gradesService.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IGradeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        } else this.errorList(res.message.messages);
      });
	}

	submit() {
		this.submitted = true;
		if (this.form.valid) {
			const model: IGradeRequest = {
				ID: this.form.value.id,
				Ar_Name: this.form.value.ar_name,
				En_Name: this.form.value.en_name,
				Min_Salary: this.form.value.miN_SALARY,
				Middel_Salary: this.form.value.middel_Salary,
				Max_Salary: this.form.value.maX_SALARY,
			};

			model.ID == 0 ? this.create(model) : this.update(model);
		}
	}

	create(model: IGradeRequest) {
		this.showLoading();
		this.gradesService.create(model).subscribe((res: IResponse<IGradeModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.lastPage, this.page.pageSize);
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	update(model: IGradeRequest) {
		this.showLoading();
		this.gradesService.update(model).subscribe((res: IResponse<IGradeModel>) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.clearForm();
				this.getList(this.currentPage, this.page.pageSize); // current page after update
				this.updateSuccessfullyMsg(res?.message?.messageTypeName);
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	gradeToEdit(grade: IGradeModel) {
		this.form.patchValue(grade);
		this.formCtrls.ar_name.setValue(grade.ar_Name);
		this.formCtrls.en_name.setValue(grade.en_Name);
	}

	onDelete(grade: IGradeModel): void {
		let msg = this.msgTranslate(grade.ar_Name, grade.en_Name);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(grade);
		});
	}

	delete(grade: IGradeModel) {
		this.showLoading();
		this.gradesService.Delete(grade.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
				if (grade.id == this.formCtrls.id.value) this.clearForm();
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	updateSalariesValidation(value: number) {
		this.checkRequiredValidator(
			"miN_SALARY",
			CustomValidators.gt(0),
			this.formCtrls.maX_SALARY.value || this.formCtrls.middel_Salary.value ? true : false
		);
		this.checkRequiredValidator(
			"maX_SALARY",
			CustomValidators.gt(this.formCtrls.miN_SALARY.value ?? 0),
			this.formCtrls.middel_Salary.value ? true : false
		);
		this.updateMiddleSalaryRange();
	}

	private checkRequiredValidator(fieldName: string, validatorsArray: any, isRequired: boolean) {
		let validators = [CustomValidators.digits, validatorsArray];
		if (isRequired) validators.push(Validators.required);
		this.formCtrls[fieldName].setValidators(validators);
		this.formCtrls[fieldName].updateValueAndValidity();
	}

	private updateMiddleSalaryRange() {
		if (this.formCtrls.miN_SALARY.value && this.formCtrls.maX_SALARY.value)
			this.checkRequiredValidator(
				"middel_Salary",
				CustomValidators.range([this.form.value.miN_SALARY, this.form.value.maX_SALARY]),
				false
			);
		else if (this.formCtrls.maX_SALARY.value)
			this.checkRequiredValidator(
				"middel_Salary",
				CustomValidators.lte(this.form.value.maX_SALARY),
				false
			);
		else
			this.checkRequiredValidator(
				"middel_Salary",
				CustomValidators.gte(this.form.value.miN_SALARY ?? 1),
				false
			);
	}
}
